body {
  background: rgb(2,0,36);
  background: -webkit-gradient(linear, left top, right top, from(rgba(2,0,36,1)), color-stop(43%, rgba(192,105,193,1)), to(rgba(0,212,255,1)));
  background: -webkit-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(192,105,193,1) 43%, rgba(0,212,255,1) 100%);
  background: -o-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(192,105,193,1) 43%, rgba(0,212,255,1) 100%);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(192,105,193,1) 43%, rgba(0,212,255,1) 100%); 
  color: white;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



